.popup__close-img {
    background: url(../../../images/close_btn.svg);
    background-repeat: no-repeat;
    background-position: center center;
    border: none;
    cursor: pointer;
    width: 40.7px;
    height: 40.7px;
    position: absolute;
    top: -47px;
    right: -45.5px;
    padding: 0;
  }
  
  .popup__close-img:hover {
    opacity: 0.6;
  }
  
  @media only screen and (max-width: 532px) {
    .popup__close-img {
      width: 20px;
      height: 20px;
      top: -20px;
      right: -20px;
    }
  }